body {
    background-color: #1c1c1e;
    font-family: 'Arial', sans-serif;
    color: white;
}

.dashboard-container {
    padding: 20px;
}

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #333;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.7);
}

.search-box {
    padding: 10px;
    font-size: 16px;
    background-color: #444;
    border: 1px solid #555;
    color: white;
    border-radius: 4px;
    width: 100%;
    max-width: 400px;
    margin-right: 40px;
}

.chart-section, .table-section, .insights-section, .buffer-section, .live-campaign-metrics {
    margin: 20px 0;
    background-color: #222;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.8);
}

.buffer-section {
    height: 200px;
}

canvas {
    margin-top: 20px;
    display: block;
    max-width: 100%;
    height: auto;
}

h1 {
    margin-left: 20px;
    color: #0ff;
}

h2 {
    color: #ff0077;
}

#d3Chart {
    height: 300px;
}

.table-section {
    overflow-x: auto;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #444;
}

th {
    background-color: #333;
    color: #0ff; 
}

td {
    color: #fff;
}

tr:nth-child(even) {
    background-color: #2a2a2d;
}

.dropdown {
    position: absolute;
    background-color: #333;
    color: white;
    padding: 10px;
    border: 1px solid #555;
    max-height: 200px;
    overflow-y: auto;
    z-index: 1000;
}

.dropdown-item {
    padding: 5px;
    cursor: pointer;
}

.dropdown-item:hover {
    background-color: #444;
}

.dropdown-arrow {
    font-size: 10px;
    margin-left: 5px;
    cursor: pointer;
}

.pagination button {
    padding: 1px 20px;
    margin-top: 27.5px;
    background-color: #0077ff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    align-items: right;
    margin-right: 0px;
    height: 30px;
    margin-left: 10px;
} 

.pagination button:hover {
    background-color: #005bb5;
}

.live-campaign-metrics {
    margin-top: 20px;
    background-color: #222;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.8);
}

.campaign-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.campaign-box {
    border: 1px solid #444;
    border-radius: 8px;
    padding: 15px;
    background-color: #2a2a2d;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    color: #ffffff;
}

.campaign-box h3 {
    font-size: 18px;
    margin-bottom: 10px;
    color: #0ff;
}

.campaign-box table {
    width: 100%;
    margin-bottom: 10px;
}

.campaign-box table td {
    padding: 4px 8px;
    border-bottom: 1px solid #555;
    color: #ddd;
}

.campaign-box table tr:nth-child(even) {
    background-color: #333;
}

.line-chart-container {
    width: 100%;
    height: 150px;
    margin-top: 10px;
}

.line-chart-container canvas {
    background-color: #222;
    border-radius: 4px;
    width: 100%;
}

.input-section {
    display: flex;
    justify-content: space-between; 
    margin: 20px 0;
    max-width: 100%;
}

.subject-selection-column {
    display: flex;
    flex-direction: column;
    margin-right: 40px; 
}

.metric-selection-row {
    display: flex;
    align-items: flex-start; 
}

.metric-selection-row > div {
    display: flex;
    flex-direction: column;
    margin-right: 40px;
}

.subject-select select, 
.metric-selection-row select {
    padding: 10px;
    border-radius: 4px;
    margin-top: 5px;
    margin-bottom: 5px;
    border: 1px solid #555;
    background-color: #444;
    color: white;
    width: 100%;
}

.subject-select label, 
.metric-selection-row label {
    color: white;
    margin-bottom: 5px;
}

.metric-selection-row button {
    padding: 1px 20px;
    margin-top: 27.5px;
    background-color: #0077ff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    align-items: right;
    margin-right: 0px;
    height: 40px;
    margin-left: 10px;
    margin-right: 15px;
}

.metric-selection-row button:hover {
    background-color: #005bb5;
}

.dynamic-chart-section {
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.dynamic-chart-section canvas {
    max-width: 100%;
    max-height: 500px; 
}
